<template>
   <div class="BXliveSession__BxGroup__BXstudyData">
       <div class="BoxTabLable">
         <span
            v-for="(n, index) in LabelTabStudent" :key="index"
            :class="{spanIsActive:index == current}"
            @click="current = index"
         >
            {{n}}
         </span>
      </div>
      <div class="BoxTabContent">
         <div v-if="current === 0" class="BXscripts">
            <StudyLevel/>
            <ProgressBar/>
         </div>
         <div v-if="current === 1" class="BXscripts">
            <CertificatePlan/>
         </div>
         <div v-show="current === 2" class="BXscripts">
            <AgoraSignaling
               :GET_DataUpcoming ="GET_DataUpcoming"
            />
         </div>
      </div>
   </div>
</template>

<script>
   import StudyLevel from '../../../student-components/studentProgressReport/StudyLevel.vue'
   import ProgressBar from '../../../student-components/studentProgressReport/ProgressBar.vue'
   import CertificatePlan from '../../../student-components/studentProgressReport/CertificatePlan.vue'
   import AgoraSignaling from '../AgoraTab/AgoraSignaling/AgoraSignaling.vue'

   export default {
      components:{
         StudyLevel,
         ProgressBar,
         CertificatePlan,
         AgoraSignaling,
      },
      props:[
         'DataUpcoming'
      ],
      data() {
         return {
            current:0,
            LabelTabStudent:['STUDY DATA', 'ACADEMIC PLAN', 'CHAT'],
         }
      },
      mounted() {
         const studentUsername = {
            username: this.$store.state.DataAdditional.username
         }
         this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
      },
      computed: {
         GET_DataUpcoming(){
            return this.DataUpcoming[0]
         },

      },
   }
</script>
<style lang="scss">
   @import '../../../views/studentProgressReport/StudentProgressReport.scss';

</style>



