<template>
	<div class="BXcountdown" >
		<div class="BxAlert">
			<v-snackbar
				v-model="snackbar"
				color="blue darken-3"
				:timeout="timeout"
				top
			>
					<p style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.8em;z-index:105;">Your Session Automatically will be directed to Session Summary</p>
				<!-- <v-btn dark flat @click="EmitExitSession">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
		<div class="status-tag timeTitle" v-bind:class="{ Warnings: isActive }"  v-if="statusType === 'running' && statusType !== 'expired'">
			{{ statusText2 }}
		</div>
			<div class="timer" v-if="statusType === 'running' && statusType !== 'expired'">
				<div class="min">
						<span class="number">{{ minutes }}</span>
				</div>
			<div class="split">
				:
			</div>
				<div class="sec">
						<span class="number">{{ seconds }}</span>
				</div>
			</div>
		
		<div v-if="statusType === 'running' && statusType !== 'expired'">
		</div>
	</div>
</template>

<script>
	export default {
		props:[
			'startTime', 'endTime', 'trans'
		],
		data () {
			return {
				isActive: false,
				timer: "",
				wordString: {},
				start: "",
				end: "",
				interval: "",
				days: "",
				minutes: "",
				hours: "",
				seconds: "",
				message: "",
				statusType: "",
				statusText: "",
				statusText2: "",
				ShowTimer:'',
				snackbar: false,
				timeout:500000
			}
		},
		created () {
			this.wordString = JSON.parse(this.trans)
		},
		mounted () {
			this.start = new Date(this.startTime).getTime()
			this.end = new Date(this.endTime).getTime()
			//Update the Count every 1 Second
			this.timerCount(this.start, this.end)
			this.interval = setInterval(() => {
				this.timerCount(this.start, this.end)
			}, 1000)
		},
		methods:{
			EmitExitSession($event) {
				this.$emit('event_child', $event)
			},
			timerCount(start, end){
				//Get todays date and time
				var now = new Date().getTime()
				//Find the distance between now and the count down date
				var distance = start - now
				var passTime = end - now
				if(distance < 0 && passTime < 0){
					this.message = this.wordString.expired
					this.statusType = "expired"
					this.statusText = this.wordString.status.expired
					clearInterval(this.interval)
					setTimeout(() => {
						window.location.reload()
					}, 1000);
					return
				}else if (distance < 0 && passTime > 0){
					this.calcTime(passTime)
					this.message = this.wordString.running
					this.statusType = "running"
					this.statusText = this.wordString.status.running
				}else if(distance > 0 && passTime > 0){
					this.calcTime(distance)
					this.message = this.wordString.upcoming
					this.statusType = "upcoming"
					this.statusType = this.wordString.status.upcoming
				}
			},
			calcTime(dist){
				// Time calculations for days, hours, minutes and seconds
				this.days = Math.floor(dist / (1000 * 60 * 60 * 24));
				this.hours = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
				this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
				this.seconds = Math.floor((dist % (1000 * 60)) / 1000);
			},
		
		},
		watch: {

			isActive(val){
				if(!val){
					return
				}else{
					this.$store.dispatch('GET_DASH_UPCOMING_SESSIONS_FOR_SESSION_SUMMARY')
				}	
			},
			
			minutes(val){

				if (val > '5' && this.days == '0' && this.hours == '0'){
					this.statusText2 = 'Time Left'
					this.isActive = false
				} else if (val < '5' && this.days == '0' && this.hours == '0'){
					this.statusText2 = 'Session Will be End'
					this.isActive = true
				}
			},
			
			seconds(val){
				if(val < 59 && this.days == '0' && this.hours == '0' && this.minutes == '0'){
					this.snackbar = true
					setTimeout(() => {
						this.$store.commit('muttalertFive', '1m');
					}, 1000);
				}
			},
			statusType(val){
				if (val === 'expired'){
					console.log(val)
					this.EmitExitSession()
				}
			}
		},
	}
</script>



